body {
                  align-items: center;
                  background-color: #fff;
                  display: flex;
                  height: 100vh;
                  justify-content: center;
              }

.App {
}

.botIcon {
    position: fixed;
    left: -200px;
    top: 180px;
    width: 400px;
}

.chatbotOutput {
    position: relative;
    font-family: Ubuntu, sans-serif;
    font-size: x-large;
    text-align: left;
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: left;
    margin-right: 4rem;
    min-width: 400px;
    width: 70%;
    max-width: 800px;
    overflow-y: auto;
    border: 0.1pt solid lightgray;
    padding: 45px;
    margin-bottom: 5%;
    background-color: rgba(255, 255, 255, 0.7);
}

.inputFieldContainer {
    position: fixed;
    margin-left: 5%;
    margin-right: 5%;

    max-width: 800px;
    width: 70%;
    top: 600px;
}

.inputField {

    position: sticky;
    z-index: 9999;

    font-family: Ubuntu, sans-serif;
    font-size: x-large;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-right: 4rem;
    padding-left: 15px;
    width: 100%;
    height: 50px;
}

input {
    width: 100%;
    padding: 5px;
}

button {
    z-index: 9999;
    position: absolute;
    right: -10px;
    top: 7px;

    width: 70px;
    height: 45px;

    -moz-appearance: none;

    border: none;
    background: #ededed;
    border-radius: 3px;
    padding: 10px;
  margin-top: 2px;

    transition: all .2s;
}

button:hover {
  background-color: #67AB67;
  color: white;
}

button:active {
  background-color: #468546;
  color: white;
}

@media only screen and (min-width: 1024px) {
    .inputFieldContainer {
        left: 20%;
    }
}

